<template>
    <div class="line">
        <el-row>
            <el-button type="primary" @click="newLine">+ 添加线路</el-button>
        </el-row>
        <div class="box">
            <p class="title">线路</p>
            <div class="tableCon">
                <el-table
                    :data="line"
                style="width: 100%">
                    <el-table-column
                        prop="routename"
                        label="线路名称"
                        width="380">
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button size="mini" type="primary" @click="myEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button size="mini" type="danger" @click="myDelete(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>

    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data () {
        return {
            line: [],
            save: {}
        }
    },
    methods: {
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log(res.data.list)
                this.line = res.data.list;
                // this.save.line = res.data.list[0]
            })
        },
        // 删除
        myDelete(index, val) {
            this.$confirm('此操作将永久删除该线路, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                api.delTrainrouteinsert(val.id).then(res => {
                    console.log(res)
                    if(res.data.stats == "success") {
                        this.getSelectAll();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    } else {
                        this.$message({
                            type: 'info',
                            message: '删除失败'
                        });  
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        // 修改
        myEdit(index, val) {
            console.log(val)
            this.save = val;
            this.$prompt('请输入线路名称', '修改站点', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputValue: val.routename
            }).then(({ value }) => {
                api.changeLine({
                    id: val.id,
                    routename: value
                }).then(res => {
                    if(res.status == 200) {
                        this.getSelectAll();
                        this.$message({
                            type: 'success',
                            message: '修改成功: ' + value
                        });
                    } else {
                        this.$message({
                            type: 'info',
                            message: '修改失败'
                        });   
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消输入'
                });       
            });
        },
        // 新增站点
        newLine() {
            this.$prompt('请输入线路名称', '新增线路', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                api.newTrainrouteinsert({routename: value}).then(res => {
                    if(res.data.stats == "success") {
                        this.getSelectAll();
                        this.$message({
                            type: 'success',
                            message: '新增成功: ' + value
                        });
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消输入'
                });       
            });
        },
    },
    created() {
        this.getSelectAll();
    },
}
</script>
<style lang="stylus" scoped>
.box
    border-radius 12px
    overflow hidden
    background #fff
    margin-top 12px
    .title 
        margin 20px
    .tableCon
        margin 20px
        border 1px solid #eee
        border-bottom none
</style>



